<template>
    <div>
        <div id="control">
            <button id="startButton">Start</button>
            <button id="stopButton">Stop</button>
        </div>
        <div id="container"></div>
    </div>
</template>

<script>
// import * as THREE from 'three';
// import 'mind-ar/dist/mindar-image-three.prod.js';
// import * as MindAR from 'mind-ar/dist/mindar-image.prod';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

export default {
    name: "MindARScene",
    mounted() {
        const THREE = window.MINDAR.IMAGE.THREE;
        const mindarThree = new window.MINDAR.IMAGE.MindARThree({
            container: document.querySelector("#container"),
            imageTargetSrc: "../../static/image-tracking/nft/coco/coco.mind"
        });
        const {renderer, scene, camera} = mindarThree;

        const anchor = mindarThree.addAnchor(0);
        const geometry = new THREE.PlaneGeometry(1, 0.484);
        const material = new THREE.MeshBasicMaterial( {color: 0x00ffff, transparent: true, opacity: 0.5} );
        const plane = new THREE.Mesh( geometry, material );
        anchor.group.add(plane);

        //////////////////////////////////////////////////////////////////////////////////
        //		add an object in the scene
        //////////////////////////////////////////////////////////////////////////////////

        // add helper
        const axesHelper = new THREE.AxesHelper(100);
        scene.add(axesHelper);

        const clock = new THREE.Clock();
        const mixers = [];

        // Load GLTF model
        const threeGLTFLoader = new GLTFLoader();
        threeGLTFLoader.load("/static/resources/Flamingo.glb", function (gltf) {
            var model = gltf.scene.children[0];
            model.name = 'Flamingo';

            var animation = gltf.animations[0];
            var mixer = new THREE.AnimationMixer(model);
            mixers.push(mixer);
            var action = mixer.clipAction(animation);
            action.play();

            anchor.group.add(model);

            // model.position.x = 100;
            model.position.y = 10;
            // model.position.z = -200;
        });        

        //////////////////////////////////////////////////////////////////////////////////
        //		add button events
        //////////////////////////////////////////////////////////////////////////////////

        const start = async() => {
            await mindarThree.start();
            renderer.setAnimationLoop(() => {
                if (mixers.length > 0) {
                    var elapsed = clock.getDelta();
                    for (var i = 0; i < mixers.length; i++) {
                        mixers[i].update(elapsed);
                    }
                }
                renderer.render(scene, camera);
            });
        }

        const startButton = document.querySelector("#startButton");
        startButton.addEventListener("click", () => {
            start();
        });

        const stopButton = document.querySelector("#stopButton");
        stopButton.addEventListener("click", () => {
            mindarThree.stop();
            mindarThree.renderer.setAnimationLoop(null);
        });
    }
}
</script>

<style>
    #container {
        width: 100vw;
        height: 100vh;
        position: relative;
        overflow: hidden;
    }
    #control {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 2;
    }
</style>



